<template>
	<view>
		<ReturnHead title="编辑" complete=""></ReturnHead>
		<view class="back-ground">
			<view class="general">
				<text class="list">头像</text>
				<image :src="itemList.photoPath" class="profile-photo"></image>				
			    <text class="iconfont icon-youjiantou1 you-icon list" @click="toPageUserhead"></text>
				<view class="line"></view>
			</view>
			<view class="general ">
				<text class="list general-2">昵称</text>
				<view class="profile-name">
					<text class="gray" >{{itemList.nickname}}</text>
					<text class="iconfont icon-youjiantou1 you-icon" @click="toPageUsername"></text>
					</view>
				<view class="line line-2"></view>
			</view>
		    <view class="general ">
				<view class="list general-2">
				<text >个性签名</text>	
				<text class="iconfont icon-youjiantou1 you-icon you-icon-2" @click="toPageUsersign"></text>
				</view>
			    <view class="line line-3"></view>
			</view>
			<view class="general ">
				<view class="list general-2">
				<text >修改密码</text>	
				<text class="iconfont icon-youjiantou1 you-icon you-icon-2" @click="toPageModifypsw"></text>
				</view>
			    <view class="line line-3"></view>
			</view>
		</view>
	</view>
</template>

<script>
	import ReturnHead from '../../../components/message/ReturnHead.vue'
	import {
	  getUserMes,
	} from "../../../axios/message/message.js";
	export default {
		onLoad: function(e){
		        // console.log(e.id); //123
		        // console.log(e.name); //张三
				// this.value=e.name;
				this.getUid();
				
		    },
		data() {
			return {
				itemList: [],
			};  
		},
		components: {
			  ReturnHead
		},
		methods:{
		    getUid(){
		    	var a = '';
				var that = this;
		    	uni.getStorage({ 
		    	key:'userInfo',
		    	success:function(res){ 
		    		a=res.data;
		    		this.userUid=a.uid;
		    		console.log(this.userUid);
		    		that.getUserMes(this.userUid);
		    		}
		    });
		    },
			toPageUserhead(){
				uni.navigateTo({
					url:'../../level3/profile/userhead?head='+this.itemList.photoPath
				})
			},
			toPageUsername(){
				uni.navigateTo({
					url:'../../level3/profile/username?name='+this.itemList.nickname
				})
			},
			toPageUsersign(){
				uni.navigateTo({
					url:'../../level3/profile/usersign?motto='+this.itemList.motto
				})
			},
			toPageModifypsw(){
				uni.navigateTo({
					url:"../../level3/profile/modifypsw"
				})
			},	
			getUserMes(uid){
					return getUserMes(uid).then(
					(res) => {	
						this.itemList =res.data.data;
						// this.detaill=res.data.data.nickname;
						// console.log(res.data.data.nickname);
						},
					(err) => {
					  console.log(err);
					}
				);
			},
		}
		};
</script>

<style scoped>
@import url("../../../../static/icons/iconfont/iconfont.css"); 
.back-ground{
	background-color: 	#fdf7ef;
	width: 100%;
	height: 100vh;
	margin-top: 36rpx;
}
.general{
	position: relative;
	left: 5%;
	width: 95%;
}
.profile-photo{
	margin-top: 20rpx;
	width: 120rpx;
	height: 120rpx;
	margin-left: 65%;
	border-radius: 20rpx;
}
.line{
	border-bottom: 3rpx solid #E4E4E4;
	margin-top: 10rpx;
}
.list{
	position: relative;
	top: -46rpx;
}
.you-icon{
	margin-left: 12rpx;
	color: #707070;
	font-size: 28rpx;
}
.you-icon-2{
	margin-left: 74%;
}
.profile-name{
	/* background-color: #007AFF; */
	width: 300rpx;
	margin-left: 54%;
	text-align: right;
	margin-top: 6rpx;
}
.gray{
	color: #707070;
}
.general-2{
	top: 55rpx;
}
.line-2{
	margin-top: 65rpx;
}
.line-3{
	margin-top: 115rpx;
}
</style>
